<template>
  <div class="apiManagement-body">
    <div class="apiManagement-above">
      <div class="apiManagement-above-body">
        <span> api名称 </span>
        <div class="apiManagement-above-body-input">
          <el-input
            v-model="input"
            placeholder="请输入api名称"
            maxlength="30px"
          />
        </div>
        <span style="margin-left: 5%"> 方法 </span>
        <div class="apiManagement-above-body-select">
          <el-select v-model="value" class="m-2" placeholder="Select">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </div>
        <div class="apiManagement-above-body-button">
          <el-button type="primary" @click="searchApi(input,value)">查询</el-button>
        </div>
      </div>
    </div>
    <div class="apiManagement-under">
      <div class="apiManagement-under-body">
        <div class="apiManagement-under-body-button">
          <el-button type="primary" @click="dialogFormVisible = true"
            >新增</el-button
          >
          <el-dialog v-model="dialogFormVisible" title="增加用户">
            <el-form :model="apiForm">
              <el-form-item label="api名称" :label-width="formLabelWidth">
                <el-input v-model="apiForm.name" autocomplete="off" />
              </el-form-item>
              <el-form-item label="URL" :label-width="formLabelWidth">
                <el-input v-model="apiForm.url" autocomplete="off" />
              </el-form-item>
              <el-form-item label="method" :label-width="formLabelWidth">
                <el-select v-model="apiForm.method">
                  <el-option label="GET" value="GET" />
                  <el-option label="POST" value="POST" />
                  <el-option label="PUT" value="PUT" />
                  <el-option label="DELETE" value="DELETE" />
                </el-select>
              </el-form-item>
              <el-form-item label="描述" :label-width="formLabelWidth">
                <el-input v-model="apiForm.desc" autocomplete="off" />
              </el-form-item>
            </el-form>
            <template #footer>
              <span class="dialog-footer">
                <el-button @click="closeWindow()">取消</el-button>
                <el-button type="primary" @click="addApi1(apiForm)">
                  确认
                </el-button>
              </span>
            </template>
          </el-dialog>
        </div>
        <div class="apiManagement-under-body-table">
          <el-table :data="apiData" style="width: 100%" max-height="50%">
            <el-ta  ble-column prop="name" label="api名称" min-width="20%" align="center"  />
            <el-table-column prop="url" label="URL" min-width="20%" align="center" />
            <el-table-column prop="method" label="方法" min-width="20%" align="center" />
            <el-table-column prop="desc" label="描述" min-width="20%" align="center" />
            <el-table-column label="操作" min-width="20%" align="center">
              <template #default="scope">
                <span style="font-size: 10px; color: #4d70ff; cursor: pointer" @click="getScope(scope.$index)"
                  >操作
                </span>
                <el-dialog v-model="dialogFormVisible1" title="增加用户">
                  <el-form :model="updateForm">
                    <el-form-item label="api名称" :label-width="formLabelWidth">
                      <el-input v-model="updateForm.name" autocomplete="off" />
                    </el-form-item>
                    <el-form-item label="URL" :label-width="formLabelWidth">
                      <el-input v-model="updateForm.url" autocomplete="off" />
                    </el-form-item>
                    <el-form-item label="method" :label-width="formLabelWidth">
                      <el-select v-model="updateForm.method">
                        <el-option label="GET" value="GET" />
                        <el-option label="POST" value="POST" />
                        <el-option label="PUT" value="PUT" />
                        <el-option label="DELETE" value="DELETE" />
                      </el-select>
                    </el-form-item>
                    <el-form-item label="描述" :label-width="formLabelWidth">
                      <el-input v-model="updateForm.desc" autocomplete="off" />
                    </el-form-item>
                  </el-form>
                  <template #footer>
                    <span class="dialog-footer">
                      <el-button @click="dialogFormVisible=false"
                        >取消</el-button
                      >
                      <el-button type="primary" @click="operationApi(updateForm)">
                        确认
                      </el-button>
                    </span>
                  </template>
                </el-dialog>
                <span
                  style="
                    font-size: 10px;
                    margin-left: 10px;
                    color: #4d70ff;
                    cursor: pointer;
                  "
                  @click="deleRow(scope.$index)"
                  >删除</span
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="apiManagement-under-body-pagination">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            @current-change="paging"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from "vue";
import { addApi } from "@/api/authorityCenter.js";
import { getData } from "@/api/authorityCenter.js";
import { ElMessage } from "element-plus";
import { deleteApi } from "@/api/authorityCenter.js";
import { updateApi } from "@/api/authorityCenter.js"
export default {
  setup() {
    let input = ref("");
    let value = ref("");
    let dialogFormVisible1 = ref(false);
    const payload = {
      currPage: "1",
      pageSize: "9",
    };
    const options = [
      {
        value: "GET",
        label: "GET",
      },
      {
        value: "POST",
        label: "POST",
      },
      {
        value: "PUT",
        label: "PUT",
      },
      {
        value: "DELETE",
        label: "DELETE",
      }
    ];
    const apiData = ref([""]);
    let total = ref("0");
    function getApiData(payload) {
      getData(payload).then((res) => {
        console.log(res);
        apiData.value = res.data.data.pageData;
        total.value = Number(res.data.data.total);
      });
    }
    let updateForm = ref({})
    let apiForm = reactive({
      name: "",
      url: "",
      method: "",
      desc: "",
    });
    function clear(){
      apiForm.name="",
      apiForm.url="",
      apiForm.method="",
      apiForm.desc=""
    }
    function closeWindow(){
      clear()
      dialogFormVisible.value = false
    }
    function getScope(index){
      dialogFormVisible1.value = true
      let updateForm1 = ref({
      _id : apiData.value[index]._id,
      name : apiData.value[index].name,
      url  : apiData.value[index].url,
      method : apiData.value[index].method,
      desc : apiData.value[index].desc
    })
    updateForm.value = updateForm1.value
    }
    function paging(val) {
      let payload2 = reactive({
        currPage: val,
        pageSize: "9",
      });
      getData(payload2).then((res) => {
        apiData.value = res.data.data.pageData;
      });
    }
    const formLabelWidth = "140px";
  
    let dialogFormVisible = ref(false);
    function addApi1(ABC) {
      console.log(ABC);
      let DEF = JSON.parse(JSON.stringify(ABC));
      console.log(DEF);
      dialogFormVisible.value = false;
      addApi(DEF).then((res) => {
        console.log(res);
        if (res.data.code == 200) {
          ElMessage({
            message: "添加成功",
            type: "success",
          });
          getApiData(payload);
        } else {
          ElMessage({
            message: "添加失败",
            type: "error",
          });
        }
      });
      clear();
    }
    function deleRow(index) {
      let params = {
        _id: apiData.value[index]._id,
      };
      console.log(apiData.value[index]._id);
      deleteApi(params).then((res) => {
        if (res.data.code == 200) {
          ElMessage({
            message: "删除成功",
            type: "success",
          });
          getApiData(payload);
        } else {
          ElMessage({
            message: "删除失败",
            type: "error",
          });
        }
      });
    }
    function operationApi(qwe){
      updateApi(qwe).then((res)=>{
        if(res.data.code==200){
          ElMessage({
            message: "更新成功",
            type: "success",
          });
          getApiData(payload);
        } else {
          ElMessage({
            message: "更新失败",
            type: "error",
          });
        }
      })
      dialogFormVisible1.value = false
    }
    function searchApi(abc,bcd){
      let payload = {
        name:abc,
        method:bcd,
        currPage: "1",
        pageSize: "9",
      }
      getData(payload).then((res) => {
        console.log(res);
        if(res.data.code==200){
          apiData.value = res.data.data.pageData
          total.value = Number(res.data.data.total);
        } else {
          ElMessage({
            message: "查询失败",
            type: "error",
          });
        }
      });
    }
    onMounted(() => {
      getApiData(payload);
    });
    return {
      input,
      value,
      options,
      apiData,
      payload,
      total,
      dialogFormVisible,
      apiForm,
      formLabelWidth,
      dialogFormVisible1,
      updateForm,
      searchApi,
      getApiData,
      paging,
      addApi1,
      deleRow,
      operationApi,
      getScope,
      clear,
      closeWindow,
    };
  },
};
</script>

<style scoped>
.apiManagement-body {
  width: 100%;
  height: 100%;
}
.apiManagement-above {
  position: absolute;
  width: 85%;
  height: 13%;
  background-color: white;
  margin-top: -2%;
}
.apiManagement-above-body {
  font-size: 15px;
  padding: 3%;
  margin-left: 3%;
  display: flex;
}
.apiManagement-above-body-input {
  display: inline;
  width: 20%;
  margin-left: 1%;
  margin-top: -0.3%;
}
.apiManagement-above-body-select {
  margin-left: 1%;
  margin-top: -0.3%;
}
.apiManagement-above-body-button {
  margin-left: 1%;
  margin-top: -0.3%;
}
.apiManagement-under {
  position: absolute;
  background-color: white;
  width: 85%;
  height: 70%;
  margin-top: 5%;
}
.apiManagement-under-body {
  display: flex;
  padding: 1%;
}
.apiManagement-under-body-button {
  display: inline;
}
.apiManagement-under-body-table {
  margin-left: -5%;
  margin-top: 4%;
  width: 100%;
  height: 80%;
  overflow: hidden;
}
.apiManagement-under-body-pagination {
  display: inline;
  margin-top: 37%;
  margin-left: -65%;
}
</style>